.hero{
    width: 100%;
    height: 100vh;
    position: relative;

}

.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;

}

.hero-mid h1{
    padding-top: 4rem !important;
}

img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.hero-text{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
}

.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    color: white;
   
}


.hero-text p{
    font-size: 1.3rem;
    
    color: white;
    padding: 0.5rem 0.5rem 2rem;
   
}

.hero-text .show{

    text-decoration: none;
    background: white;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: black;
}

@media screen and (max-width: 555px){
    .hero-text{
        font-size: 2rem;
        padding: 8px 10px;
    }

    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }

    .hero-text .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
      
     
    }
}